import Header from "../../components/Header.vue";
import Footer from "../../components/Footer.vue";
import Condividi from "../../components/Condividi.vue";
import DataHeader from "../../components/DataHeader.vue";
import { Paginazione } from "../../../commons/js/render_paginazione.js";
import { Utils } from "../../../commons/js/utils.js";
import { VistaGallerie, AnteprimaGalleria } from "../../js/gallerie.js";
import { ConsiglioInforma } from "../../js/consiglioinforma";

export default {
    name: "app",
    components: {
        Footer,
        Header,
        Condividi,
        DataHeader
    },
    data: function () {
        $(document).ready(renderGalleria);
        return {};
    }
};


function renderGalleria() {

    var urlParams = new URLSearchParams(window.location.search);
    const tipo = urlParams.get('tipo');

    var url_to_call = apibase.baseurl + '/10536' + '?size=12'
    url_to_call += Paginazione.inizializzaUrl(urlParams);
    const filtro = {
        "SORT": [{
            "direction": "DESC",
            "property": "date"
        }]
    };
    if (tipo) {
        filtro.type = tipo;
    }
    renderBreadcrumb();
    $.ajax({
        url: url_to_call,
        method: 'POST',
        data: JSON.stringify(filtro),
        contentType: 'application/json',
        dataType: 'json',
        success: render
    })

    function renderBreadcrumb() {
        let bcSezione = '<li class="breadcrumb-item">Video</li>';
        if (tipo) {
            bcSezione = '<li class="breadcrumb-item">\
                            <a href="galleria-video.html">Video</a>\
                            <span class="separator">/</span>\
                        </li>\
                        <li class="breadcrumb-item">' + ConsiglioInforma.Video[tipo] + '</li>';
        }
        $('#bread').append(bcSezione);
    }

    function render(result) {
        if (result != null && result.content.length > 0) {
            let html = [];
            result.content.forEach(function (video) {
                let immagine = {};
                if (video.components.image && video.components.image.components.length) {
                    immagine = {
                        titolo: video.components.image.components[0].title,
                        src: Utils.creaUrlImmagine(video.components.image.components[0].properties.thumbaddress)
                    }
                    immagine.src = Utils.creaPlaceholder(immagine.src, '//via.placeholder.com/512x320');
                }
                let anteprima = new AnteprimaGalleria();
                anteprima.srcImmagine = immagine.src;
                anteprima.titoloImmagine = immagine.titolo;
                anteprima.linkOggetto = "video.html?id=" + video.id;
                anteprima.titoloOggetto = video.title;
                html.push('<div class="col-12 col-lg-6">' + VistaGallerie.renderAnteprimaGalleria(anteprima) + '</div>');
            });
            $('#fascia_elenco').append('<div class="row">' + html.join('') + '</div>');
            $("#fascia_elenco").append(Paginazione.renderizzaComponente(result));
        } else {
            $("#fascia_elenco").html("Nessun risultato trovato");
        }
    }


}



